<template>
    <div class="p-editor-container">
        <div ref="toolbarElement" class="p-editor-toolbar">
            <slot name="toolbar">
                <span class="ql-formats">
                    <select class="ql-header" defaultValue="0">
                        <option value="1">Heading</option>
                        <option value="2">Subheading</option>
                        <option value="0">Normal</option>
                    </select>
                    <select class="ql-font"  >
                        
                        <option v-for="font in fontNames" :key="font.index" :value="font" ></option>
                        
                    </select>
                </span>
                <span class="ql-formats">
                    <button class="ql-bold" type="button"></button>
                    <button class="ql-italic" type="button"></button>
                    <button class="ql-underline" type="button"></button>
                </span>
                <span class="ql-formats">
                    <select class="ql-color"></select>
                    <select class="ql-background"></select>
                </span>
                <span class="ql-formats">
                    <button class="ql-list" value="ordered" type="button"></button>
                    <button class="ql-list" value="bullet" type="button"></button>
                    <select class="ql-align">
                        <option defaultValue></option>
                        <option value="center"></option>
                        <option value="right"></option>
                        <option value="justify"></option>
                    </select>
                </span>
                <span class="ql-formats">
                    <button class="ql-link" type="button"></button>
                    <button class="ql-image" type="button"></button>
                    <button class="ql-code-block" type="button"></button>
                </span>
                <span class="ql-formats">
                    <button class="ql-clean" type="button"></button>
                </span>
            </slot>
        </div>
        <div ref="editorElement" class="p-editor-content" :style="editorStyle"></div>
    </div>
    


</template>

<script>
import Quill from "quill";
export default {
    name: 'Editor',
    data() {
        return {
            fonts:['Arial', 'Courier', 'Garamond', 'Tahoma', 'Times New Roman', 'Verdana','Nikosh'],
            fontNames:null

        }
    },
    emits: ['update:modelValue', 'text-change'],
    props: {
        modelValue: String,
        placeholder: String,
        readonly: Boolean,
        formats: Array,
        editorStyle: null,
        
    },
    quill: null,
    watch: {
        modelValue(newValue, oldValue) {
            if (newValue !== oldValue && this.quill && !this.quill.hasFocus()) {
                this.renderValue(newValue);
            }
        }
    },
    created () {
 
 this.fontNames = this.fonts.map(font => this.getFontName(font)); 

    },
    mounted() {

function getFontName(font) {
    return font.toLowerCase().replace(/\s/g, "-");
}

// add fonts to style
var fontStyles = "";
this.fonts.forEach(function(font) {
    var fontName = getFontName(font);
    fontStyles += ".ql-snow .ql-picker.ql-font .ql-picker-label[data-value=" + fontName + "]::before, .ql-snow .ql-picker.ql-font .ql-picker-item[data-value=" + fontName + "]::before {" +
        "content: '" + font + "';" +
        "font-family: '" + font + "', sans-serif;" +
        "}" +
        ".ql-font-" + fontName + "{" +
        " font-family: '" + font + "', sans-serif;" +
        "}";
});
var node = document.createElement('style');
node.innerHTML = fontStyles;
document.body.appendChild(node);

        
        let Font = Quill.import("attributors/style/font");
Font.whitelist = this.fontNames;
Quill.register(Font, true);
        this.quill = new Quill(this.$refs.editorElement, {
            modules: {
                toolbar: this.$refs.toolbarElement
            },
            readOnly: this.readonly,
            theme: 'snow',
            formats: this.formats,
            placeholder: this.placeholder
        });
        this.renderValue(this.modelValue);
        this.quill.on('text-change', (delta, oldContents, source) => {
            if (source === 'user') {
                let html = this.$refs.editorElement.children[0].innerHTML;
                let text = this.quill.getText().trim();
                if (html === '<p><br></p>') {
                    html = '';
                }
                this.$emit('update:modelValue', html);
                this.$emit('text-change', {
                    htmlValue: html,
                    textValue: text,
                    delta: delta,
                    source: source,
                    instance: this.quill
                });
            }
        });
    },
    methods: {
getFontName(font) {
    return font.toLowerCase().replace(/\s/g, "-");
},
        renderValue(value) {
            if (this.quill) {
                if (value)
                    this.quill.pasteHTML(value);
                else
                    this.quill.setText('');
            }
        }
    },
    beforeUnmount() {
        this.quill = null;
    }
}
</script>

